<template>
  <div class="results-container">
    <div class="confetti" aria-hidden="true">
      <span
        v-for="piece in confettiPieces"
        :key="piece.id"
        class="confetti-piece"
        :style="piece.style"
      ></span>
    </div>

    <div class="message-card">
      <h1>Danke fürs mitmachen!</h1>
      <p>Deine Stimmen wurden aufgezeichnet, die Auflösung ist um XX Uhr!</p>

      <div class="vote-summary">
        <h2>Deine Stimmen</h2>
        <div v-if="summaryItems.length === 0" class="summary-empty">
          No votes recorded yet.
        </div>
        <ul v-else class="summary-list">
          <li v-for="item in summaryItems" :key="item.category">
            <img
              :src="item.image"
              alt="Selected costume"
              class="summary-image"
            />
            <div class="summary-text">
              <span class="summary-label">{{ categoryLabel(item.category) }}:</span>
              <span class="summary-value">{{ item.name }}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { useCompleteStore } from '@/store/store';

export default {
  name: 'ResultsView',

  data() {
    return {
      confettiPieces: []
    };
  },

  computed: {
    store() {
      return useCompleteStore();
    },
    summaryItems() {
      const selections = this.store.getVotedSelections || {};
      return [1, 2, 3]
        .map((category) => {
          const selectedId = selections[category];
          if (!selectedId) return null;

          const list = this.store.getCostumesByCategory(category);
          const match = list.find((c) => c.id === selectedId);
          if (!match) return null;

          return {
            category,
            name: match.name,
            image: match.image
          };
        })
        .filter(Boolean);
    }
  },

  mounted() {
    this.confettiPieces = this.buildConfettiPieces(60);
  },

  methods: {
    categoryLabel(category) {
      if (category === 1) return 'Bestes Einzelkost\u00FCm';
      if (category === 2) return 'Bestes Gruppenkost\u00FCm';
      if (category === 3) return 'Kreativstes Kost\u00FCm';
      return `Category ${category}`;
    },
    buildConfettiPieces(count) {
      const colors = [
        '#ff3b30',
        '#ffcc00',
        '#34c759',
        '#007aff',
        '#af52de',
        '#ff9500'
      ];

      return Array.from({ length: count }, (_, index) => {
        const size = 6 + Math.random() * 8;
        const left = Math.random() * 100;
        const delay = Math.random() * 1.5;
        const duration = 2.5 + Math.random() * 2.5;
        const rotate = Math.floor(Math.random() * 360);
        const color = colors[Math.floor(Math.random() * colors.length)];

        return {
          id: index,
          style: {
            left: `${left}%`,
            width: `${size}px`,
            height: `${size * 0.4}px`,
            background: color,
            animationDelay: `${delay}s`,
            animationDuration: `${duration}s`,
            transform: `rotate(${rotate}deg)`
          }
        };
      });
    }
  }
};
</script>

